  import Scroll from "../../base/scroll/scroll.vue"
  import Step from "../../base/step/step.vue"
  import Upload from "../../base/upload/upload.vue"

  export default {
    name: "getPrice1",
    components: {
      Step,
      Upload,
      Scroll
    },
    data() {
      return {
        stepArr: ["", ""],
        stepIndex: 1,
        isOk: true, //
        commonImg: [],
        twoInfo: {}
      }
    },
    //    
    created() {
      let that = this;
//      
      this.$axios.post('/api/orderOperate/getAdditionalInfo', {
        buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
      }).then((res) => {
        let data = res.data
        if (data.code === "200") {
          this.twoInfo = data.data
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title: '',
          content: '',
        })
      })
    },
//    
    methods: {
      next() {
        let that = this;
        if(this.commonImg.length>0){
          this.$axios.post('/api/orderOperate/backOrderSubmit', {
            buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
            text:that.$route.query.text,
            commonImg:this.commonImg
          }).then((res) => {
            console.log(res)
            if (res.data.code == 200) {
              this.$router.push({ name: 'submitSuccess',query:{type:'evaluate4'} })
            }
          })
        }else{
          this.$vux.alert.show({
            title:'',
            content: '',
          })
        }
      }
    }
  }



